@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{font-family:'Lato';padding-left:200px;padding-right:200px;background-color:#F9EDED}

p{margin:0px;padding:0px}button{border:none}button:focus{outline:none}.homeContainer{display:flex;flex-direction:row}.homeLeft{height:100vh;margin-top:18%;font-weight:900;font-size:18px;color:#424242}.homeRight{margin-top:5%;margin:auto}.homeTextColor{color:#FF697C}.homeHeader{font-size:90px;font-weight:700;margin:0}.homeDescriptionText{font-weight:500;font-size:14px;color:#908F8F}.homeButtonContainer{margin-top:45px}.homeButton{border-radius:30px;padding:6px;padding-left:24px;padding-right:24px;border:3px solid;font-size:14px;cursor:pointer;letter-spacing:1px;font-weight:700}.homeButtonResume{color:#fff;background-color:#FF697C;border-color:#FF697C;margin-right:14px;transition:all 0.4s ease-in-out}.homeButtonResume:hover{color:#fff;box-shadow:0 5px 15px #ff697c}.homeButtonChat{color:#FF697C;background-color:transparent;border-color:#FF697C;transition:all 0.4s ease-in-out}.homeButtonChat:hover{box-shadow:0 5px 15px #ff697c}

.aboutContainer{padding-bottom:100px;width:100%}.aboutSubHeader{color:#FF697C;font-size:14px;font-weight:700;letter-spacing:1.5px}.aboutMainHeader{color:#424242;font-size:30px;font-weight:900}.aboutListContainer{margin-top:40px;display:flex;flex-direction:row;justify-content:space-between}

.aboutCardContainer{width:300px;height:100%}.aboutCardHeader{color:#424242;font-size:18px;font-weight:900;margin-top:18px}.aboutCardDescription{color:#908F8F;font-size:13px;font-weight:500;line-height:1.75;margin-top:8px}.aboutCardIcon{height:40px}

.projectsContainer{padding-bottom:100px}.projectsSubHeader{color:#FF697C;font-size:14px;font-weight:700}.projectsMainHeader{color:#424242;font-size:30px;font-weight:900}.projectsListContainer{margin-top:40px;display:flex;flex-direction:row;justify-content:space-between}

a{text-decoration:none}.projectsCardContainer{width:315px;height:100%;border-radius:6px;background-color:#fff}.projectsCardTextContainer{padding:15px;padding-top:2px;padding-bottom:10px}.projectsCardSubHeader{font-size:9.5px;font-weight:900;color:#908F8F;height:9.5px}.projectsCardDescription{font-size:14px;font-weight:900;color:#424242;margin-top:6px;margin-bottom:8px}.projectsCardLink{color:#FF697C;font-size:12px;font-weight:900;transition:all 0.3s ease-in-out}.projectsCardLink:hover{opacity:0.7}

.tlCardContainer{background-color:#fff;padding:18px;border-radius:6px;text-align:left !important;margin-right:15px;margin-left:15px}.tlCardTitle{margin:0;font-size:18px;font-weight:900;color:#424242}.tlCardDate{margin:0;margin-top:2px;color:#FF697C;font-size:14px;font-weight:500}.tlCardDescription{color:#908F8F;font-size:13px;font-weight:500;line-height:1.75;margin-top:8px}

.experienceContianer{padding-top:40px;padding-bottom:40px;height:100%}.experienceDot{background-color:green}

.tlContainer{padding-bottom:80px}.tlSubHeader{color:#FF697C;font-size:14px;font-weight:700;letter-spacing:1.5px}.tlMainHeader{color:#424242;font-size:30px;font-weight:900}.tlExperienceContainer{margin-top:40px}

.footerContainer{display:flex;padding-bottom:40px}.footerInnerContainer{margin:auto}.footerGithub{margin-right:50px}.footerEmail{margin-left:50px}.footerGithub,.footerLinkedin,.footerEmail{transition:all 0.3s ease-in-out}.footerGithub:hover,.footerLinkedin:hover,.footerEmail:hover{opacity:0.7;cursor:pointer}

